<template>

  <div class="c-content">

    <div class="c-header">
      <div class="c-div-row" ref="aa">
        <span class="c-span_large">主图</span><span class="c-span_large" :class="{ 'c-span_large-select': isShowMA }"
                                                  @click="showMA(true)">MA</span><span class="c-span_large"
                                                                                       :class="{ 'c-span_large-select': !isShowMA }"
                                                                                       @click="showMA(false)">隐藏</span>
      </div>
      <div class="c-div-row">
        <span class="c-span_large">副图</span>
        <span class="c-span_large" :class="{ 'c-span_large-select': isShowSTO }"
              @click="showSTO(true)">STO</span>
        <span class="c-span_large"
              :class="{ 'c-span_large-select': !isShowSTO }"
              @click="showSTO(false)">隐藏</span>
      </div>
      <div class="c-div-row">
        <span class="c-span_large">分时</span>
        <span class="c-span_middle" v-for="item in maData">{{item.name}}</span>
      </div>
      <div class="c-div-row">
        <span class="c-span_price">28341.2/28352.4</span><span class="c-push" @click="showPopup(true)">推送<van-icon
        name="arrow-up"/></span>
      </div>
    </div>
    <div ref="kcontent" class="K-line"></div>
    <div class="c-popou" v-show="isShowPopup" @click="showPopup(false)">

      <div class="c-popou-content">
        <ul>
          <li v-for="item in  maData">

            <div style="padding: 2px 4px">
              <span>{{item.name}}</span>
              <van-switch @click.stop size="12px" style="float: right" v-model="item.isAccept"></van-switch>

            </div>

          </li>

        </ul>

      </div>

    </div>
  </div>
</template>

<script>


  import echarts from "echarts";

  import kline from "../lib/k-line";
  import tmpData from "../lib/tmpData";


  export default {
    name: "page1",
    data() {
      return {

        isShowMA: true,
        isShowSTO: true,
        isShowPopup: false,
        kChart:{},

        maData: [
          {
            name: "M1",
            isAccept: true
          },
          {
            name: "M5",
            isAccept: true
          },
          {
            name: "M30",
            isAccept: true
          },
          {
            name: "H1",
            isAccept: true
          },
          {
            name: "H4",
            isAccept: true
          },
          {
            name: "D1",
            isAccept: true
          },
          {
            name: "W1",
            isAccept: true
          },
          {
            name: "MN",
            isAccept: true
          }
        ]
      };
    },


    methods: {
      showMA(isShow) {

        this.isShowMA = isShow;
        if (isShow){

          this.kChart.setOption({
            legend:{
              selected:{
                'MA5':true,
                'MA10':true,
                'MA30':true,
              }
            }
          });


        } else {
          this.kChart.setOption({
            legend:{
              selected:{
                'MA5':false,
                'MA10':false,
                'MA30':false,
              }
            }
          });
        }
      },
      showSTO(isShow) {

        this.isShowSTO = isShow;
        if (isShow){

          this.kChart.setOption({
            legend:{
              selected:{
                'DEA':true,

              }
            }
          });


        } else {
          this.kChart.setOption({
            legend:{
              selected:{
                'DEA':false,

              }
            }
          });
        }
      },
      showPopup(isShow) {

        this.isShowPopup = isShow;
      }

    },
    created() {


      this.$nextTick(() => {
        // let mChart = echarts.init(this.$refs.mline);
        // mChart.setOption(kline.initMOption(tmpData.mdata, "us"));

        this.kChart = echarts.init(this.$refs.kcontent);
        this.kChart.setOption(kline.initKOption(tmpData.kdata));
      });

    },

    components: {}
  };
</script>

<style scoped>

  .c-content {
    background-color: #1f212d;
    height: 100%;
    width: 100%;
  }

  .c-header {
    padding: 10px;

  }

  .c-popou {

    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.5);

  }

  .c-popou-content {
    position: absolute;
    top: 120px;
    right: 20px;
    height: auto;
    width: 100px;
    background-color: white;
  }

  .K-line {
    height: calc(100% - 126px);
    width: 100%;
  }

  .c-span_large {
    font-size: 16px;
    color: #fdfdfd;
    margin-right: 10px;
  }

  .c-span_large-select {
    color: #F9293E;
  }

  .c-span_middle {
    font-size: 14px;
    color: #fdfdfd;
    margin-right: 10px;
  }

  .c-span_price {
    font-size: 18px;
    color: #55a532;
    margin-right: 10px;
  }

  .c-div-row {
    margin-bottom: 10px;
  }

  .c-push {
    color: white;
    font-size: 12px;
    background-color: #909399;
    border-radius: 9px;
    padding: 4px 6px;
    float: right;
    margin-right: 10px;
  }
</style>



